package de.kampfkalender.web.components

import mui.material.List as MaterialList
import csstype.AlignItems
import csstype.FlexShrink
import csstype.JustifyContent
import csstype.em
import csstype.pct
import csstype.px
import csstype.rem
import csstype.vh
import de.kampfkalender.common.ExportedEvent
import de.kampfkalender.web.ApplicationContext
import de.kampfkalender.web.utils.formatAsDate
import de.kampfkalender.web.utils.formatAsTime
import de.kampfkalender.web.utils.getLogo
import de.kampfkalender.web.utils.getLogoHeight
import kotlinext.js.jso
import mui.icons.material.ArrowRight
import mui.material.Box
import mui.material.Button
import mui.material.Dialog
import mui.material.DialogActions
import mui.material.DialogContent
import mui.material.DialogScroll
import mui.material.DialogTitle
import mui.material.Link
import mui.material.LinkUnderline
import mui.material.ListItem
import mui.material.ListItemIcon
import mui.material.Stack
import mui.material.StackDirection
import mui.material.Tooltip
import mui.material.Typography
import mui.system.ResponsiveStyleValue
import org.w3c.dom.url.URL
import react.FC
import react.Props
import react.ReactNode
import react.createElement
import react.dom.html.AnchorTarget
import react.dom.html.ReactHTML
import react.key
import react.useContext

external interface EventDialogProps : Props {
    var open: Boolean?
    var event: ExportedEvent?
    var onClose: () -> Unit
}

val EventDialog = FC<EventDialogProps> { props ->
    if (props.event == null) {
        return@FC
    }

    val app = useContext(ApplicationContext)
    val event = props.event!!
    val date = event.time.formatAsDate()
    val timeRange = event.time.formatAsTime() + " - " + event.until.formatAsTime() + " Uhr"

    val closeDialog: () -> Unit = {
        props.onClose()
    }

    Dialog {
        open = props.open == true
        keepMounted = false
        maxWidth = "md"
        fullWidth = true
        scroll = DialogScroll.paper
        onClose = { _, _ -> closeDialog() }

        DialogTitle {

            // title & chips left, date & time right
            Stack {
                sx = jso {
                    justifyContent = JustifyContent.spaceBetween
                    alignItems = AlignItems.start
                }

                direction = ResponsiveStyleValue(StackDirection.row)
                spacing = ResponsiveStyleValue(0.5.rem)

                // title & chips
                Box {
                    +event.title

                    Box {
                        sx = jso {
                            marginTop = 0.5.rem
                        }

                        EventTags {
                            this.event = event
                        }
                    }
                }

                // date & time
                Stack {
                    sx = jso {
                        flexShrink = FlexShrink(0.0)
                        alignItems = AlignItems.end
                    }

                    direction = ResponsiveStyleValue(StackDirection.column)
                    //spacing = ResponsiveStyleValue(0.5.rem)

                    Typography {
                        +date
                    }
                    Typography {
                        +timeRange
                    }
                }
            }
        }

        DialogContent {
            dividers = true

            Stack {
                direction = ResponsiveStyleValue(StackDirection.column)
                spacing = ResponsiveStyleValue(1.em)

                Box {
                    val youtube = event.broadcasters.firstNotNullOfOrNull {
                        if (it.value.url?.startsWith("https://www.youtube.com/watch?v", true) == true) {
                            it
                        } else {
                            null
                        }
                    }

                    val youtubeUrl = if (youtube!=null) {
                        try {
                            URL(youtube.value.url!!)
                        } catch (_: Throwable) {
                            null
                        }
                    } else {
                        null
                    }

                    val youtubeEmbedded = youtubeUrl != null && youtubeUrl.searchParams.has("v")
                    if (youtubeUrl != null && youtubeEmbedded) {
                        val videoId = youtubeUrl.searchParams.get("v")!!

                        +createElement(ReactHTML.iframe, jso {
                            style = jso {
                                aspectRatio = "16 / 9".asDynamic()
                                width = 100.pct
                            }

                            src = "https://www.youtube-nocookie.com/embed/$videoId"
                            @Suppress("DEPRECATION")
                            frameBorder = 0
                            allow =
                                "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen = true
                        })
                    }

                    val broadcasters = event.broadcasters.filter {
                        !youtubeEmbedded || it.key != youtube?.key
                    }

                    if (broadcasters.isNotEmpty()) {
                        Typography {
                            sx = jso {
                                marginBottom = 10.px
                            }

                            component = ReactHTML.h2
                            variant = "h6"

                            if (youtubeEmbedded) {
                                +"Oder anschauen bei"
                            } else {
                                +"Anschauen bei"
                            }
                        }

                        Stack {
                            direction = ResponsiveStyleValue(StackDirection.row)
                            spacing = ResponsiveStyleValue(1.em)

                            broadcasters.forEach {
                                key = it.key.name

                                val logo = it.key.getLogo(app.darkMode)
                                if (logo == null) {
                                    +it.key.title
                                    return@forEach
                                }

                                Tooltip {
                                    title = ReactNode(it.key.title)

                                    Link {
                                        href = it.value.url ?: it.key.url ?: "#"
                                        target = AnchorTarget._blank

                                        +createElement(ReactHTML.img, jso {
                                            alt = it.key.title
                                            src = "/logos/$logo"
                                            style = jso {
                                                //width = 100.pct
                                                height = 100.vh
                                                maxHeight = (it.key.getLogoHeight() * 2).px
                                                //maxWidth = 100.px
                                            }
                                        })
                                    }
                                }
                            }
                        }
                    }
                }

                if (!event.promotion.podcast) {
                    Box {
                        Typography {
                            component = ReactHTML.h2
                            variant = "h6"

                            +"Fight Card"
                        }
                        if (event.card.isEmpty()) {
                            +"Die Fight Card für das Event ist leider nicht bekannt."
                        } else {
                            MaterialList {
                                event.card.forEach {
                                    ListItem {
                                        sx = jso {
                                            padding = 0.rem
                                        }

                                        ListItemIcon {
                                            sx = jso {
                                                minWidth = 35.px
                                            }
                                            ArrowRight()
                                        }

                                        +it
                                    }
                                }
                            }
                        }
                    }
                }

                if (event.links.isNotEmpty()) {
                    Box {
                        Typography {
                            component = ReactHTML.h2
                            variant = "h6"

                            +"Weitere Links"
                        }

                        MaterialList {
                            event.links.forEach {
                                val url = try {
                                    URL(it)
                                } catch (_: Throwable) {
                                    return@forEach
                                }

                                ListItem {
                                    sx = jso {
                                        padding = 0.rem
                                    }

                                    ListItemIcon {
                                        sx = jso {
                                            minWidth = 35.px
                                        }
                                        ArrowRight()
                                    }

                                    Link {
                                        href = it
                                        target = AnchorTarget._blank
                                        underline = LinkUnderline.hover
                                        +url.hostname
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        DialogActions {
            Button {
                onClick = {
                    closeDialog()
                }

                +"Schließen"
            }
        }
    }
}
