package de.kampfkalender.web.components

import csstype.FlexWrap
import csstype.ListStyle
import csstype.rem
import de.kampfkalender.common.ExportedEvent
import kotlinext.js.jso
import mui.material.Chip
import mui.material.ChipColor
import mui.material.ListItem
import mui.material.Size
import mui.material.Stack
import mui.material.StackDirection
import mui.system.ResponsiveStyleValue
import react.FC
import react.Props
import react.ReactNode
import react.dom.html.ReactHTML
import react.key

external interface EventTagsProps : Props {
    var event: ExportedEvent
}

val EventTags = FC<EventTagsProps> { props ->
    Stack {
        sx = jso {
            flexWrap = FlexWrap.wrap
            listStyle = ListStyle.none
            padding = 0.rem
            marginTop = (-0.4).rem
            marginLeft = (-0.3).rem
            marginBottom = 0.rem
            marginRight = 0.rem
        }

        component = ReactHTML.ul
        direction = ResponsiveStyleValue(StackDirection.row)

        if (props.event.promotion.podcast) {
            EventTag {
                key = "is-podcast"
                title = "Podcast"
                color = ChipColor.primary
            }
        }

        props.event.sports.forEach {
            EventTag {
                key = it.name
                title = it.title
                color = ChipColor.primary
            }
        }

        if (props.event.live) {
            EventTag {
                key = "is-live"
                title = "live"
                color = ChipColor.secondary
            }
        }

        if (props.event.isFree()) {
            EventTag {
                key = "is-free"
                title = "kostenlos"
                color = ChipColor.success
            }
        }
    }
}

private external interface EventTagProps : Props {
    var title: String
    var color: ChipColor
}

private val EventTag = FC<EventTagProps> { props ->
    ListItem {
        sx = jso {
            padding = 0.rem
            marginTop = 0.4.rem
            marginLeft = 0.3.rem
            width = "auto".asDynamic()
        }

        // key = props.key
        disableGutters = true
        Chip {
            color = props.color
            label = ReactNode(props.title)
            size = Size.small
        }
    }
}
